@font-face {
  font-family: "Helvetica";
  src: url("../fonts/HelveticaBold.woff2") format("woff2"), url("../fonts/HelveticaBold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Helvetica";
  src: url("../fonts/HelveticaRegular.woff2") format("woff2"), url("../fonts/HelveticaRegular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

*,
*::after,
*::before {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  font-family: "Helvetica", "Arial", sans-serif;
  color: #1a1a1a; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.site-wrapper {
  position: relative;
  max-width: 1280px;
  margin: 0 auto; }

.site-wrapper__main {
  position: relative;
  margin-left: 280px; }

.aside {
  width: 280px;
  height: 100%;
  position: fixed;
  z-index: 1;
  overflow-y: auto;
  padding-top: 22px;
  padding-bottom: 22px;
  box-shadow: 0px 0px 24px rgba(0, 0, 0, 0.06); }

.aside__logo {
  display: block;
  margin-bottom: 24px;
  margin-left: 19px; }

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-bottom: 5px;
  border-bottom: 1px solid #e6e4e7; }

.site-list__link {
  padding-left: 16px;
  padding-right: 20px;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 24px;
  color: #1a1a1a;
  text-decoration: none; }
  .site-list__link:hover, .site-list__link:focus {
    background-color: #fdeaf1; }

.site-list__link-icon {
  margin-right: 6px; }

.site-list__link-arrow {
  margin-left: auto; }

.purchase-list {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-top: 7px; }

.purchase-list__link {
  padding-left: 16px;
  padding-right: 16px;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 24px;
  color: #1a1a1a;
  text-decoration: none; }
  .purchase-list__link:hover, .purchase-list__link:focus {
    background-color: #fdeaf1; }

.purchase-list__link-icon {
  margin-right: 6px; }

.purchase-list__link-arrow {
  margin-left: auto; }

.page-header {
  background-color: #f2f1f3;
  position: sticky;
  top: 0;
  right: 0;
  z-index: 5; }

.page-header__wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 12px;
  padding-bottom: 12px; }

.page-header__location {
  display: flex; }

.page-header__location-city,
.page-header__location-shops {
  display: flex;
  font-size: 14px;
  line-height: 24px;
  color: #1a1a1a;
  text-decoration: none; }

.page-header__location-city {
  margin-right: 25px; }

.page-header__location-city span {
  margin-right: 5px; }

.page-header__location-arrow {
  transform: rotate(90deg); }

.page-header__location-shops:hover, .page-header__location-shops:focus {
  color: #ee2a74; }

.page-header__location-pin {
  margin-right: 4px; }

.page-header__contacts {
  display: flex;
  align-items: center; }

.page-header__contacts-phone {
  margin-right: 23px;
  font-size: 14px;
  line-height: 24px;
  color: #1a1a1a;
  text-decoration: none; }
  .page-header__contacts-phone:hover, .page-header__contacts-phone:focus {
    color: #ee2a74; }

.page-header__contacts-callback {
  font: inherit;
  font-size: 14px;
  line-height: 24px;
  background-color: transparent;
  border-radius: 8px;
  border: 1px solid #1a1a1a;
  padding: 11px 16px;
  cursor: pointer; }
  .page-header__contacts-callback:hover, .page-header__contacts-callback:focus {
    background-color: #ee2a74;
    border-color: #ee2a74;
    color: #ffffff; }

.user-section {
  display: flex;
  padding: 16px 40px; }

.form {
  flex-grow: 1;
  position: relative; }

.form__search {
  width: 100%;
  padding: 7px 36px 7px 16px;
  border-radius: 8px;
  border: 1px solid #E6E4E7;
  font: inherit;
  font-size: 14px;
  line-height: 24px;
  min-height: 40px;
  box-sizing: border-box;
  outline: none;
  cursor: pointer;
  color: #1a1a1a; }
  .form__search:hover, .form__search:focus, .form__search:active {
    border-color: #ee2a74; }

.form__button {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ee2a74;
  border: none;
  width: 40px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  right: 0;
  top: 0;
  min-height: 40px;
  cursor: pointer;
  outline: none; }

.user-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 218px;
  margin-left: 24px; }

.user-list__link {
  position: relative;
  display: flex;
  align-items: center;
  font: inherit;
  font-size: 14px;
  line-height: 24px;
  color: #1a1a1a;
  text-decoration: none; }

.user-list__count {
  top: -5px;
  left: 12px;
  font-size: 9px;
  line-height: 10px;
  position: absolute;
  padding: 2px;
  width: 17px;
  height: 17px;
  background-color: #ee2a74;
  border-radius: 100%;
  border: 1px solid #ffffff;
  color: #ffffff; }

.user-list__link-icon {
  margin-right: 4px; }
  .user-list__link-icon--basket {
    margin-right: 10px; }
